




























import Vue from 'vue';

export default Vue.extend({
	props: {
		title: {
			type: String,
			default: 'Default title'
		},
		editable: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		isSmall(): boolean {
			return this.$vuetify.breakpoint.smAndDown;
		}
	}
});
