export default Vue.extend({
	components: { OptionCard, Account, Security, PaymentMethods, Subscriptions, Notifications },
	data() {
		return {
			selected: 0
		};
	},
	methods: {
		select(selected: number): void {
			this.selected = selected;
		}
	}
